import React from "react";
import { Stack, Typography } from "@material-ui/core";
import { styled } from "@material-ui/styles";
import { FieldProps } from "formik";
import Select, { OptionsType, ValueType } from "react-select";
import { OptionType } from "../utils/data";

interface CustomSelectProps extends FieldProps {
  options: OptionsType<OptionType>;
  isMulti?: boolean;
  className?: string;
  placeholder?: string;
}

export const CustomSelectGoal = ({
  placeholder,
  field,
  form,
  options,
  isMulti = false,
}: CustomSelectProps) => {
  const onChange = (option: ValueType<OptionType | OptionType[], boolean>) => {
    form.setFieldValue(
      field.name,
      isMulti
        ? (option as OptionType[]).map((item: OptionType) => item.value)
        : (option as OptionType).value
    );
    form.setFieldValue(
      "topic",
      isMulti
        ? (option as OptionType[]).map((item: OptionType) => item.label)
        : (option as OptionType).value
    );
  };

  const getValue = () => {
    if (options) {
      return isMulti
        ? options.filter((option) => field.value.indexOf(option.value) >= 0)
        : options.find((option) => option.value === field.value);
    } else {
      return isMulti ? [] : ("" as any);
    }
  };
  const { touched, errors } = form;

  return (
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="center"
      spacing={3}
    >
      <Select
        name={field.name}
        value={getValue()}
        onChange={onChange}
        placeholder={placeholder}
        options={options}
        isMulti={isMulti}
        closeMenuOnSelect={false}
      />
      {touched[field.name] && errors[field.name] && (
        <ErrorText variant="body1">{errors[field.name]}</ErrorText>
      )}
    </Stack>
  );
};

export default CustomSelectGoal;
const ErrorText = styled(Typography)({
  color: "red",
  fontSize: 12,
});
