import { Checkbox, FormControlLabel, Typography } from "@material-ui/core";
import React from "react";
import { FieldProps } from "formik";
import { Box } from "@material-ui/system";
import { useStyles } from "./AlertNotification";

interface CheckBoxFormik extends FieldProps {
  label?: string;
  sub: string;
}
const CheckBoxFormik = ({ field, form, label, sub }: CheckBoxFormik) => {
  const classes = useStyles();
  return (
    <Box className={classes.boxPaddingAlert}>
      <FormControlLabel
        control={<Checkbox {...field} color="primary" checked={field.value} />}
        label={label}
      />
      <Typography className={classes.pTagStlAlert} align="left" gutterBottom>
        {sub}
      </Typography>
    </Box>
  );
};

export default CheckBoxFormik;
