import { FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
import { FieldProps } from "formik";
import React, { useEffect,useRef } from "react";
import { CountryType, AddFunction } from "../../../utils/typeHelpers";
import useAxios from "../../../utils/useAxios";

import { MessageSuccess } from "../../../components/AlertMessageSuccess";
import { MessageError } from "../../../components/AlertMessageError";

interface CustomSelectProps extends FieldProps {
  options: CountryType[];
  isMulti?: boolean;
  className?: string;
  placeholder?: string;
}

const SelectFieldCountry = ({ field, options, form }: CustomSelectProps) => {
  const refError = useRef<null | AddFunction>(null);
  const handleCreateError = (msg:any) => {
    refError.current?.(msg);
  };


  const axiosInstance = useAxios();
  useEffect(() => {
    axiosInstance.get(`country-code/${field.value}`).then((res) => {
      if (res.status === 200) {
        form.setFieldValue("phone_number", `+${res.data.country_code}`);
      } else {
        handleCreateError("Api Status not ok!");
      }
    });
  }, [field.value]);
  return (
    <FormControl fullWidth>

      <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />

      <InputLabel id="demo-simple-select-label">Where are you from?</InputLabel>
      {options !== null && (
        <Select
          labelId="demo-simple-select-label"
          label="Select Your Country"
          id="demo-simple-select"
          {...field}
          fullWidth
        >
          {options.map((item) => (
            <MenuItem value={item.id}>{item.name}</MenuItem>
          ))}
        </Select>
      )}
    </FormControl>
  );
};

export default SelectFieldCountry;
