import React, { useEffect } from "react";
import { FieldProps } from "formik";
import { styled } from "@material-ui/styles";
import { Stack, Typography, Box, Grid } from "@material-ui/core";

import AddImage from "../../../assets/icons/imageadd.svg";
import { useSelector } from "react-redux";
import { RootAppState } from "../../../utils/typeHelpers";

interface ImageFieldProps extends FieldProps {
  imagepath?: string | null | undefined;
  profile_type: string;
}

const ImageField = ({ field, form, profile_type }: ImageFieldProps) => {
  const { errors } = form;
  const ngoID = useSelector(
    (state: RootAppState) => state.AuthReducer.ngo_profile_id
  );
  const [result, setResult] = React.useState<string | null | undefined>(null);
  useEffect(() => {
    if (field.value) {
      setResult(URL.createObjectURL(field.value));
    }
  }, [field.value]);

  return (
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="center"
      spacing={1}
    >
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
      >
        <Grid item xs={6}>
          <Box component="span" m={1} className="ovalImage">
            {result !== null ? (
              <img className="ovalImage" src={result} alt="logoaddimage" />
            ) : (
              <img className="addimageicon" src={AddImage} alt="logoaddimage" />
            )}
          </Box>
        </Grid>
        <Grid
          item
          direction="row"
          justifyContent="center"
          alignItems="center"
          xs={6}
        >
          <input
            id="file"
            name="file"
            type="file"
            onChange={(event: any) => {
              form.setFieldValue("image", event.currentTarget.files[0]);

              form.setFieldValue("profile_type", profile_type);
              if (ngoID !== null) {
                form.setFieldValue("ngo_profile_id", ngoID.toString());
              }
            }}
          />
          <Typography variant="subtitle1" className="filesizelabel">
            JPG, GIF or PNG.
          </Typography>
        </Grid>
      </Grid>

      {errors[field.name] && (
        <ErrorText variant="body1">{errors[field.name]}</ErrorText>
      )}
    </Stack>
  );
};

export default ImageField;

const BoxImage = styled(Box)({
  backgroundColor: "#CED4DA",
  width: 150,
  height: 150,
  borderRadius: 11,
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
});
const ErrorText = styled(Typography)({
  color: "red",
  fontSize: 12,
});
const ProdImage = styled("img")({
  height: 120,
  width: 80,
});
