import React, { useState, useEffect, SyntheticEvent, useRef } from "react";
import {
  Button,
  CircularProgress,
  Stack,
  Stepper,
  Step,
  StepLabel,
  AlertColor,
  Snackbar,
  Alert,
} from "@material-ui/core";
import { styled } from "@material-ui/styles";
import { useLocation, useNavigate } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { Form, Formik, FormikHelpers } from "formik";
import { Box } from "@material-ui/system";
import { useMutation, UseMutationResult } from "react-query";

import {
  AddFunction,
  CreateProfileResponseNgoOrDonee,
  CreateProfileTypeNgoOrDonee,
  ILoginResponse,
  RootAppState,
} from "../../../../utils/typeHelpers";
import { CreateProfileSchemaNgo } from "../../../../utils/formSchemas";

import { CREATE_PROFILENGOORDONE_API } from "../../../../utils/Api";
import { ErrorBoundary } from "react-error-boundary";
import PageErrorBoundary from "../../../Errror/PageErrorBoundary";

import { createProfileTypeAction } from "../../../../redux/actions/createProfileTypeAction";

import Step1Form from "../../Components/Step1Form";
import { textInputsInfoNgo } from "../../utils/data";
import AlertNotification from "../../Components/AlertNotification";
import PhotoVerification from "../../Components/PhotoVerification";
import { createProfileInitialValueNgo } from "../../../../utils/initialvalues";
import Loader from "../../../../components/Loader";
import { MessageError } from "../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../components/AlertMessageSuccess";
import { saveUserInfoAction } from "../../../../redux/actions/saveUserInfoAction";
import { saveLoginAction } from "../../../../redux/actions/loginAction";
import { saveNgoProfileIDaction } from "../../../../redux/actions/saveNgoProfileIDaction";

interface ObjectType {
  [key: string]: any;
}

const CreateProfileNgo = () => {
  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const userInfo = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo
  );

  //hooks//api mutation
  const mutationCreate: UseMutationResult<
    CreateProfileResponseNgoOrDonee,
    Error,
    CreateProfileTypeNgoOrDonee
  > = useMutation((infoProfile: CreateProfileTypeNgoOrDonee) =>
    CREATE_PROFILENGOORDONE_API(infoProfile, userInfo.access)
  );
  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useDispatch();

  //variables
  const { isError, isLoading, isSuccess, data } = mutationCreate;
  const state: ObjectType = location.state ? location.state : { password: "" };

  let [activeStep, setActiveStep] = useState(0);
  const [explode, setExplode] = useState(false);
  const label = "Create Profile NGO";
  const steps = ["Basic Info", "Notifications", "Info Validation"];

  const [initialValuesProfile, setInitialValuesProfile] =
    useState<CreateProfileTypeNgoOrDonee>({
      ...createProfileInitialValueNgo,
    });

  const selectedValidationSchema = CreateProfileSchemaNgo[activeStep];
  const isLast = activeStep === steps.length - 1;

  const sleep = (time: number) => {
    return new Promise((resolve) => setTimeout(resolve, time));
  };

  //submitform
  const submitForm = async (
    values: CreateProfileTypeNgoOrDonee,
    actions: FormikHelpers<CreateProfileTypeNgoOrDonee>
  ) => {
    await sleep(200);
    setInitialValuesProfile(values);
    mutationCreate.mutate(values);
    actions.setSubmitting(false);
  };

  //change step and validate fields of form
  const handleSubmit = (
    values: CreateProfileTypeNgoOrDonee,
    actions: FormikHelpers<CreateProfileTypeNgoOrDonee>
  ) => {
    if (isLast) {
      submitForm(values, actions);
    } else {
      setActiveStep((prev) => prev + 1);
      actions.setTouched({});
      actions.setSubmitting(false);
    }
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };
  console.log(initialValuesProfile);

  useEffect(() => {
    let didCancel = false;

    if (isSuccess) {
      if (data !== undefined) {
        if (data.email !== undefined) {
          if (data.email[0] === "profile with this email already exists.") {
            handleCreateError("Profile with this email already exists.");
          } else if (
            typeof data.username === "string" &&
            typeof data.email === "string"
          ) {
            const userInfoData: ILoginResponse = {
              refresh: userInfo.refresh,
              access: userInfo.access,
              is_account_created: true,
              user_id: userInfo.user_id,
              user_profile: {
                id: data.id,
                profile_username: data.username,
                profile_image: data.image,
                profile_type: data.profile_type,
                user_username: "",
                ngo_username: "",
              },
            };

            handleCreateSucess(
              "Success Profile Has been Created.\n We will take you to HomePage now."
            );
            setTimeout(() => {
              dispatch(saveNgoProfileIDaction(""));
              dispatch(saveUserInfoAction(userInfoData));
              dispatch(saveLoginAction(true));
              navigate("../", { replace: true });
            }, 7000);
          }
        } else if (data.username !== undefined) {
          if (
            data.username[0] === "profile with this username already exists."
          ) {
            handleCreateError("Profile with this username already exists.");
          }
        } else if (data.phone_number !== undefined) {
          if (
            data.phone_number[0] === "The phone number entered is not valid."
          ) {
            handleCreateError("The phone number entered is not valid.");
          }
        }
      }
    }
    return () => {
      didCancel = true;
    };
  }, [isError, isSuccess]);

  if (isLoading) {
    return <Loader />;
  }
  return (
    <ErrorBoundary
      fallbackRender={({ error, resetErrorBoundary }) => (
        <PageErrorBoundary
          error={error}
          resetErrorBoundary={resetErrorBoundary}
        />
      )}
      onReset={() => setExplode(false)}
      resetKeys={[explode]}
    >
      <RootContainerStyle>
        <MessageError
          children={(add: AddFunction) => {
            refError.current = add;
          }}
        />
        <MessageSuccess
          children={(add: AddFunction) => {
            refSuccess.current = add;
          }}
        />
        <Stepper activeStep={activeStep}>
          {steps.map((step, index) => {
            return (
              <Step key={step}>
                <StepLabel>{step}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
        <Formik
          enableReinitialize
          initialValues={initialValuesProfile}
          validationSchema={selectedValidationSchema}
          onSubmit={handleSubmit}
        >
          {(formikProps) => (
            <Form>
              <Stack spacing={2}>
                {renderStepContent(activeStep, label)}
                <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                  {activeStep > 0 && (
                    <Button onClick={() => setActiveStep((prev) => prev - 1)}>
                      Back
                    </Button>
                  )}

                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    disabled={formikProps.isSubmitting}
                  >
                    {isLast ? "Create Profile" : "Next"}
                  </Button>
                  {formikProps.isSubmitting && <CircularProgress />}
                </Box>
              </Stack>
            </Form>
          )}
        </Formik>
      </RootContainerStyle>
    </ErrorBoundary>
  );
};

export default CreateProfileNgo;

export const renderStepContent = (step: number, label: string) => {
  switch (step) {
    case 0:
      return (
        <Step1Form
          label={label}
          textInputsInfo={textInputsInfoNgo}
          profile_type="NGO"
        />
      );

    case 1:
      return <AlertNotification />;
    case 2:
      return <PhotoVerification />;
  }
};

const RootContainerStyle = styled("div")({
  padding: 10,
});
