import { Typography, Container } from "@material-ui/core";
import { Field } from "formik";
import React, { useState, useEffect } from "react";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";

// import { GET_COUNTRIES_API, GET_SDGS_API } from "../../../utils/Api";
import {
  CountryType,
  inputType,
  RootAppState,
  SdgsType,
} from "../../../utils/typeHelpers";
import CustomSelectGoal from "../../Home/Common/CreateGoal/Components/CustomSelectgoal";
import { OptionType } from "../../Home/Common/CreateGoal/utils/data";

import BioInputFieldProfile from "./BioInputField";
import ImageField from "./ImageField";
import InputFieldProfile from "./InputFieldProfile";
import SelectFieldCountry from "./SelectFieldCountry";

import useAxios from "../../../utils/useAxios";
import Loader from "../../../components/Loader";
import ErrorCatchComponent from "../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../Errror/ApiErrorPage";

interface Step1Form {
  label: string;
  profile_type?: string;
  textInputsInfo: inputType[];
}

const Step1Form = ({ label, textInputsInfo, profile_type }: Step1Form) => {
  const accesstoken = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  const axiosInstance = useAxios();
  const {
    data: options,
    isLoading,
    isError,
    error,
  }: UseQueryResult<CountryType[], Error> = useQuery(
    ["getCountries", accesstoken],
    async () => {
      const response = await axiosInstance.get(`/country-list/`);

      return response.data;
    }
  );

  const {
    data: optionsSdgs,
    isLoading: isLoadingSdgs,
    isError: isErrorSdgs,
    error: errorSdgs,
  }: UseQueryResult<SdgsType[], Error> = useQuery(
    ["getSdgs", accesstoken],
    async () => {
      const response = await axiosInstance.get(`/sdgs-list/`);

      return response.data;
    }
  );

  const [sdgs, setSdgs] = useState<OptionType[]>([]);

  useEffect(() => {
    if (optionsSdgs) {
      if (optionsSdgs?.length > 1) {
        const newArr: OptionType[] = optionsSdgs.map((item: SdgsType) => {
          return {
            value: item.id,
            label: item.title,
          };
        });
        setSdgs(newArr);
      }
    }
  }, [optionsSdgs]);

  // if (isLoading || isLoadingSdgs) {
  //   return <Loader />;
  // }

  return (
    <ErrorCatchComponent>
      {isLoading === false || isLoadingSdgs === false ? (
        <div>
          {isError || isErrorSdgs ? (
            <ApiErrorPage
              error={error ? error : new Error("Network response was not ok.")}
            />
          ) : (
            <Container maxWidth="sm">
              <Typography variant="h4" gutterBottom>
                {label}
              </Typography>
              <Field
                name="image"
                component={ImageField}
                type="input"
                profile_type={profile_type}
              />

              {textInputsInfo.map((item: inputType) =>
                item.label === "Location" ? (
                  <div style={{ margin: 5 }} key={item.name}>
                    {options && (
                      <Field
                        name={item.name}
                        options={options}
                        component={SelectFieldCountry}
                        placeholder="Select your country"
                        isMulti={false}
                      />
                    )}
                  </div>
                ) : (
                  <div style={{ margin: 5 }} key={item.name}>
                    <Field
                      name={item.name}
                      component={InputFieldProfile}
                      label={item.label}
                      placeholder={item.placeholder}
                      type={item.placeholder}
                    />
                  </div>
                )
              )}
              <div style={{ margin: 5 }}>
                <Field
                  name="bio"
                  component={BioInputFieldProfile}
                  label="Bio"
                  placeholder="Enter Your Info"
                />
              </div>

              <div style={{ margin: 10 }}>
                <Field
                  name="sdgs"
                  options={sdgs}
                  component={CustomSelectGoal}
                  placeholder="Select SDGS"
                  isMulti={true}
                />
              </div>
            </Container>
          )}
        </div>
      ) : (
        <Loader />
      )}
    </ErrorCatchComponent>
  );
};

export default Step1Form;
