import * as yup from "yup";
import { goalFormModel } from "./goal-model";
const FILE_SIZE = 5 * 1000000;
const VIDEO_SIZE = 250 * 1000000;
const SUPPORTED_FORMATS = [
  "image/jpg",
  "image/jpeg",
  "image/gif",
  "image/png",
  "image/PNG",
  "image/webp",
  "image/svg",
];

const SignUpValidaionSchema = yup.object().shape({
  email: yup.string().email().required("Email is required"),
  password: yup
    .string()
    .required("Password is required")
    .min(6, "Minimum 6 Characters")
    .when("email", {
      is: (val: string) => (val && val.length > 6 ? true : false),
      then: yup.string().not([yup.ref("email")], "Password can not be email"),
    }),
  confirmPassword: yup.string().when("password", {
    is: (val: string) => (val && val.length > 0 ? true : false),
    then: yup
      .string()
      .oneOf([yup.ref("password")], "Both password need to be the same"),
  }),
});

const LoginValidaionSchema = yup.object().shape({
  email: yup.string().email().required("Email is required"),
  password: yup
    .string()
    .required("Password is required")
    .min(6, "Minimum 6 Characters")
    .when("email", {
      is: (val: string) => (val && val.length > 6 ? true : false),
      then: yup.string().not([yup.ref("email")], "Password can not be email"),
    }),
});

const CreateProfileSchemaDonor = [
  yup.object().shape({
    username: yup
      .string()
      .matches(/^(\S+$)/, "This field cannot contain  blankspaces")
      .required("Field is required")
      .min(6, "Minimum 6 Charachters"),
    full_name: yup
      .string()
      .required("Field is required")
      .when("username", {
        is: (val: string) => (val && val.length > 1 ? true : false),
        then: yup
          .string()
          .not([yup.ref("username")], "Full name can not be username"),
      }),
    country: yup.number().required("Country is required"),
    phone_number: yup
      .string()
      .matches(/^(\+)[0-9]{7,16}$/, "Enter correct number!")
      .required("Phone number is required"),

    bio: yup
      .string()
      .required("Bio is required")
      .max(100, "Maximum 100 Charachters")
      .when("username", {
        is: (val: string) => (val && val.length > 1 ? true : false),
        then: yup
          .string()
          .not([yup.ref("username")], "Bio name can not be username"),
      }),
    image: yup
      .mixed()
      .required("Profile Image is required")
      .test(
        "fileSize",
        "Maximum Size 5MB",
        (value) => value && value.size <= FILE_SIZE
      )
      .test(
        "fileFormat",
        "Invalid Image",
        (value) => value && SUPPORTED_FORMATS.includes(value.type)
      ),
  }),
  yup.object().shape({
    donee_notification: yup
      .boolean()
      .required("donee_notification is required"),
    account_activity: yup.boolean().required("account_activity is required"),
    donee_activity: yup.boolean().required("donee_activity is required"),
    achieved_goals: yup.boolean().required("achieved_goals is required"),
    new_followers: yup.boolean().required("new_followers is required"),
    NGO_role_assign: yup.boolean().required("user_notification is required"),
  }),
];
