const {
  formField: {
    file,
    goalname,
    shortGoal,
    topics,
    product,
    productlink,
    productunitcost,
    productunitnumber,
    productsubtotal,
    // productservicefee,

    productpgwfee,
    productngofee,
    productplatformfee,

    productgoaltotal,
    videofile,
    full_description,
  },
} = goalFormModel;

const SUPPORTED_FORMATS_VIDEO = ["video/mp4", "video/mov"];
const validationSchemaGoal = [
  yup.object().shape({
    [file.name]: yup
      .mixed()
      .required(file.requiredErrorMsg)
      .test(
        "fileSize",
        file.fileMaxError,
        (value) => value && value.size <= FILE_SIZE
      )
      .test(
        "fileFormat",
        file.fileFormatError,
        (value) => value && SUPPORTED_FORMATS.includes(value.type)
      ),
    [goalname.name]: yup
      .string()
      .required(goalname.requiredErrorMsg)
      .min(10, goalname.minError)
      .max(25, goalname.maxError),
    [shortGoal.name]: yup.string().required(shortGoal.requiredErrorMsg),
    [topics.name]: yup
      .array()
      .required(topics.requiredErrorMsg)
      .min(1, topics.minError),
  }),
  yup.object().shape({
    [product.name]: yup.string().required(product.requiredErrorMsg),
    [productlink.name]: yup
      .string()
      .matches(
        /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/,
        "Enter correct url!"
      ),
    [productunitcost.name]: yup
      .number()
      .min(1, productunitcost.minError)
      .required(productunitcost.requiredErrorMsg),
    [productunitnumber.name]: yup
      .number()
      .min(1, productunitnumber.minError)
      .required(productunitnumber.requiredErrorMsg),
    [productsubtotal.name]: yup
      .number()
      .required(productsubtotal.requiredErrorMsg),
    // [productservicefee.name]: yup
    //   .number()
    //   .required(productservicefee.requiredErrorMsg),

    [productpgwfee.name]: yup.number().required(productpgwfee.requiredErrorMsg),

    [productngofee.name]: yup.number().required(productngofee.requiredErrorMsg),

    [productplatformfee.name]: yup
      .number()
      .required(productplatformfee.requiredErrorMsg),

    [productgoaltotal.name]: yup
      .number()
      .required(productgoaltotal.requiredErrorMsg),
  }),
  yup.object().shape({
    [videofile.name]: yup
      .array(
        yup
          .mixed()
          .required(videofile.requiredErrorMsg)
          .test(
            "fileSize",
            videofile.fileMaxError,
            (value) => value && value.size <= VIDEO_SIZE
          )
          .test(
            "fileFormat",
            videofile.fileFormatError,
            (value) => value && SUPPORTED_FORMATS_VIDEO.includes(value.type)
          )
      )
      .min(1, "1 video is minimum"),
    [full_description.name]: yup
      .string()
      .required(full_description.requiredErrorMsg),
  }),
];

export {
  SignUpValidaionSchema,
  LoginValidaionSchema,
  CreateProfileSchemaDonor,
  CreateProfileSchemaNgo,
  CreateProfileSchemaDonee,
  validationSchemaGoal,
};
